import React from 'react';
import {Link} from 'react-router-dom';
import {MainTitle} from '../general/misc/Titles';

export default function Contact() {
    return (
        <section id="contact">
            <div className="container">
                <MainTitle subtitle="Kontakt" title="Schreib mir doch mal ;) h oke">
                    Du hast noch Fragen? Erreiche uns schnell direkt ber Discord oder 'zwitscher' uns was via Twitter!
                    Wir freuen uns ber jede Nachricht und jedes neue, glckliche Mitglied auf unserem Server. Gerne
                    auch auf YouTube abonnieren (fr 'ne Custom URL).
                </MainTitle>
                <Link to="/twitter-support" className="main">
                    Twitter -{' '}
                </Link>
                <Link to="/yt" className="main">
                    YouTube -{' '}
                </Link>
                <Link to="/discord" className="main">
                    Discord -{' '}
                </Link>
                <Link to="/mail" className="main">
                    Mail
                </Link>
            </div>
        </section>
    );
}



