import React from 'react';
import {MainTitle} from '../../../general/misc/Titles';

export default function Header() {
    return (
        <section id="intro">
            <div className="container">
                <MainTitle
                    subtitle="Hey!"
                    title="Wir sind Designhub"
                    link="/discord"
                    linktitle="Komm auf unseren Discord"
                >
                    Designhub bringt talentierte Designer und Developer zu einer groen Community zusammen. Komm jetzt
                    auf unseren Discord, um deine kreative Arbeit zu prsentieren, Neues zu entdecken oder halt einfach
                    nur um mit deinen nicht vorhandenen Freunden zu reden (und natrlich auch um zu spamen).
                </MainTitle>
            </div>
        </section>
    );
}



