import React from 'react';
import {MainTitle} from '../../general/misc/Titles';
import LevelEntries from './Entries';

export default function Levels() {
    return (
        <div>
            <section id="leaderboard">
                <div className="container">
                    <MainTitle title="Leaderboard" subtitle="Levels">
                        Pro Nachricht auf unserem Discord erhlst du eine zufllige Menge XP zwischen 15 und 20. Um Spam
                        zu vermeiden, kannst du nur einmal pro Minute XP bekommen. - Das heit, je aktiver du bist,
                        desto besser wirst du im Ranking platziert! Auf unserem Discord Server kannst du mit !rank
                        deinen aktuellen Platz im Ranking sowie dein Level erfahren.
                        <br />
                        <br />
                        <h3>Coins</h3>
                        Jedes mal, wenn ihr eine gerade Levelanzahl erreicht, bekommst du automatisch 100 und zweimal
                        dein aktuelles Level als Coins. Mit Coins kannst du sinnlose Commands wie !report nutzen sowie
                        dir z.B. fr 50 Coins eine berraschungskiste kaufen. Darin erwarten dich <b>*hust*</b> TOLLE
                        <b> *hust*</b> berraschungen, wie z.B. Gutschein-Codes auf den Kauf deines nchsten Designs!
                    </MainTitle>
                    <LevelEntries />
                </div>
            </section>
        </div>
    );
}



